<template>
  <body id="login-img">
    <el-form ref="loginForm" :model="form" :rules="rules" label-width="80px" hide-required-asterisk="false" class="login-box">
      <h3 class="login-title">欢迎来到管理系统</h3>
      <el-form-item label="账号" prop="adminName">
        <el-input type="text" placeholder="请输入账号" v-model="form.adminName"/>
      </el-form-item>
      <el-form-item label="密码" prop="adminPassword">
        <el-input type="password" placeholder="请输入密码" v-model="form.adminPassword"/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" v-on:click="onSubmit('loginForm')">管理人员登录</el-button>
        <el-button type="primary" v-on:click="onSubmitTwo('loginForm')">普通员工登录</el-button>
      </el-form-item>
    </el-form>

    <el-dialog
      title="温馨提示"
      :visible.sync="dialogVisible"
      width="30%"
      :modal-append-to-body="false"
      >
      <span>请输入账号和密码</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </body>
</template>

<script>
  export default {
    name: "Login",
    data() {
      return {
        form: {
          adminName: '',
          adminPassword: ''
        },

        // 表单验证，需要在 el-form-item 元素中增加 prop 属性
        rules: {
          adminName: [
            {required: true, message: '账号不可为空', trigger: 'blur'}
          ],
          adminPassword: [
            {required: true, message: '密码不可为空', trigger: 'blur'}
          ]
        },

        formTwo: {
          userName: '',
          userPassword: ''
        },

        // 对话框显示和隐藏
        dialogVisible: false
      }
    },
    methods: {
      onSubmit(formName) {
        // 为表单绑定验证功能
        this.$refs[formName].validate(async valid => {
          if (valid) {
            //console.log(this.loginForm)
            // 使用 vue-router 路由到指定页面，该方式称之为编程式导航
            const {data:res} =await this.$http.post("api/login",this.form);
            if(res.code!==200) return this.$message.error("登录失败");
            else{
              sessionStorage.setItem("adminName",this.form.adminName);
              sessionStorage.setItem("token", 'true');
              this.$router.push("/main");}
          } else {
            this.dialogVisible = true;
            return false;
          }
        });
      },
      onSubmitTwo(formName) {
        this.$refs[formName].validate(async valid => {
          if (valid) {
            //console.log(this.loginForm)
            // 使用 vue-router 路由到指定页面，该方式称之为编程式导航
            this.formTwo.userName=this.form.adminName;
            this.formTwo.userPassword=this.form.adminPassword;
            const {data:res} =await this.$http.post("api/loginTwo",this.formTwo);
            if(res.code!==200) return this.$message.error("登录失败");
            else {
              sessionStorage.setItem("userName",this.formTwo.userName);
              sessionStorage.setItem("token", 'true');
              this.$router.push("/mainTwo");
            }
          } else {
            this.dialogVisible = true;
            return false;
          }
        });
        sessionStorage.setItem("testKey","测试")
      },
      handleClose(done){
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    }
  }
</script>

<style lang="scss" scoped>
  #login-img{
    background: url(../assets/timg.jpg) no-repeat;
    //background-position: center;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed;

  }
  .login-box {
    border: 1px solid #DCDFE6;
    width: 350px;
    margin: 180px auto;
    padding: 35px 35px 15px 35px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    box-shadow: 0 0 25px #909399;
    background: #fff;
  }

  .login-title {
    text-align: center;
    margin: 0 auto 40px auto;
    color: #303133;
  }
</style>



